import "./CorrectGuessModal.scss";
import { Modal } from "react-bootstrap";
import { Guess, GuessProps } from "./Guess";
import FormatTime from "./TimeFormatter";

type CorrectGuessModalProps = {
  hours: number;
  minutes: number;
  seconds: number;
  modalProps?: any;
  guessProps: GuessProps;
};

function CorrectGuessModal(props: CorrectGuessModalProps) {
  return (
    <Modal centered {...props.modalProps}>
      <Modal.Header className="CorrectGuessModalHeader">
        <Modal.Title>🎉Congratulations!🎉</Modal.Title>
      </Modal.Header>
      <Modal.Body className="CorrectGuessModalBody">
        <div style={{ margin: "1em" }}>
          <Guess {...props.guessProps} />
        </div>
        <div style={{ margin: "1em" }}>
          Your guess was correct! Your time was:
        </div>
        <div className="CorrectGuessModalTime">
          {FormatTime(props.hours, props.minutes, props.seconds)}
        </div>
      </Modal.Body>
    </Modal>
  );
}

export default CorrectGuessModal;
